<template>
  <div class="logistics-container">
    <van-cell-group>
      <van-cell center>
        <template #title>
          <span class="custom-title">中通快递</span>
        </template>
        <template #label>
          <span class="orders_id">快递单号：{{form.orders_id}}</span>
          <span class="copy" @click="copy(form.orders_id)">复制</span>
        </template>
        <template #icon>
          <van-image
            width="50"
            height="40"
            src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=2283283292,840657002&fm=26&gp=0.jpg"
          />
        </template>
      </van-cell>
      <van-cell center>
        <template #title>
          <div class="title">订单编号：20209384889384888</div>
        </template>
        <template #label>
          <div class="text" id="text">收货地址：上海市杨浦区延吉中路幸福小区333弄80号903室</div>
        </template>
        <template #icon>
          <div class="wrapper"></div>
        </template>
      </van-cell>
      <van-cell>
        <van-steps direction="vertical" :active="0" active-color="#A57C3C">
          <van-step>
            <h3>【城市】物流状态1</h3>
            <p>2016-07-12 12:40</p>
          </van-step>
          <van-step>
            <h3>【城市】物流状态2</h3>
            <p>2016-07-11 10:00</p>
          </van-step>
          <van-step>
            <h3>快件已发货</h3>
            <p>2016-07-10 09:30</p>
          </van-step>
        </van-steps>
      </van-cell>
    </van-cell-group>
    <div class="mall_content">
      <div class="c_item">
        <div class="c_item_image"></div>
        <div class="c_item_text">
          <div class="c_item_text_head">
            <div class="c_item_text_head_title">苍术（已炒制）</div>
            <div class="c_item_text_head_tag">
              <i class="iconitem iconfont icontianjia"></i>
            </div>
          </div>
          <div class="c_item_text_describe">
            <span class="c_item_text_describe_left">¥69.9</span>
            <span class="c_item_text_describe_right">包邮</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      form: {
        orders_id: 127400274930,
      },
    };
  },
  watch: {},
  computed: {},
  methods: {
    init(div) {
      var len = 16; //默认显示字数
      var ctn = div; //获取div对象
      var content = ctn.innerHTML.replace(/\s*/g, ""); //获取div里的内容

      //alert(content);
      var span = document.createElement("span"); //创建<span>元素
      var a = document.createElement("a"); //创建<a>元素
      span.innerHTML = content.substring(0, len); //span里的内容为content的前len个字符

      a.innerHTML =
        content.length > len
          ? "... 展开<i class='iconfont iconxia' style='font-size: 12px;'></i>"
          : ""; ////判断显示的字数是否大于默认显示的字数    来设置a的显示
      a.style.cssText = `color:#333333`;
      a.href = "javascript:void(0)"; //让a链接点击不跳转

      a.onclick = function () {
        if (a.innerHTML.indexOf("展开") > 0) {
          //如果a中含有"展开"则显示"收起"
          a.innerHTML =
            "收起<i class='iconfont iconxia' style='display: -webkit-inline-box;transform: rotate(180deg);font-size: 12px;'></i>";
          a.style.cssText = `color:#333333;margin-left:4px;`;
          span.innerHTML = content;
        } else {
          a.innerHTML =
            "...展开<i class='iconfont iconxia' style='font-size: 12px;'></i>";
          a.style.cssText = `color:#333333;margin-left:0;`;
          span.innerHTML = content.substring(0, len);
        }
      };
      // 设置div内容为空，span元素 a元素加入到div中
      ctn.innerHTML = "";
      ctn.appendChild(span);
      ctn.appendChild(a);
    },
    copy(str) {
      let oInput = document.createElement("input");
      oInput.value = str;
      document.body.appendChild(oInput);
      oInput.select();
      document.execCommand("Copy");
      oInput.style.display = "none";
      document.body.removeChild(oInput);
      this.$toast("已复制到剪切板");
    },
  },
  created() {},
  mounted() {
    let desc = document.getElementsByClassName("text");
    for (let i = 0; i < desc.length; i++) {
      this.init(desc[i]);
    }
  },
};
</script>
<style lang="scss" scoped>
.logistics-container {
  /deep/ .van-cell__title {
    margin-left: 10px;
  }
  .wrapper {
    width: 50px;
    height: 40px;
  }
  .custom-title {
    color: #ad823e;
  }
  .title {
    color: #000000;
  }
  .orders_id {
    color: #333333;
    font-size: 14px;
  }
  .text {
    color: #333333;
    font-size: 14px;
    line-height: 24px;
  }
  .copy {
    margin-left: 5px;
    padding: 3px 6px;
    color: #333;
    border-radius: 3px;
    border: 1px solid #a7a7a7;
  }
  .mall_content {
    width: 100%;
    height: auto;
    margin-top: 8px;
    padding: 15px 15px 0;
    background-color: #fff;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .mall_content_title {
      margin: 15px 0;
      width: 100%;
    }
    .c_item {
      width: 49%;
      margin-bottom: 4%;
      height: 200px;
      border-radius: 5px;
      overflow: hidden;
      // border: solid 1px #dcdcdc;
      .c_item_image {
        width: 100%;
        height: 65%;
        background-color: #f7f3ed;
      }
      .c_item_text {
        width: 100%;
        height: 35%;
        padding: 10px;
        border-bottom: 1px solid #dcdcdc;
        border-right: 1px solid #dcdcdc;
        border-left: 1px solid #dcdcdc;
        border-radius: 0px 0px 5px 5px;
        .c_item_text_head {
          // font-size: 14px;
          // margin-bottom: 10px;

          width: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 5px;
          .c_item_text_head_title {
            font-size: 14px;
          }
          .c_item_text_head_tag {
            display: inline-flex;
            align-items: center;
            .iconitem {
              font-size: 25px;
              color: #7e3330;
            }
          }
        }
        .c_item_text_describe {
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
          .c_item_text_describe_left {
            color: #79312e;
          }
          .c_item_text_describe_right {
            color: #666666;
            font-size: 12px;
          }
        }
      }
    }
  }
}
</style>


